    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			return {
				memberInfo:[],
				sysnum:0,
				artlist:[],
				allfavnum:0, //收藏数 
				pinglunnum:0 //评价数
			}				
		},
        computed: mapState(['hasLogin','userInfo']),  
		onLoad() {
			 console.log('页面加载 ');
			this.loadData();
			
		},
		onShow() {
             console.log('页面显示..');
			  if (!this.hasLogin) {  
			 	uni.navigateTo({
			 		url:'login',
			 	})
			 }
			
			//this.memberInfo = this.userInfo;
			
         },
         onReady(){
             console.log('页面初次显示');
         },
         onHide() {
             console.log('页面隐藏');
         },
         onUnload() {
             console.log('页面卸载');
         },
         onBackPress(){
             console.log('页面返回...');
         },
         onShareAppMessage() {
             console.log('分享!');
         },
         onReachBottom() {
             console.log('下拉加载...');
         },
         onPageScroll(){
             console.log('页面滚动...');
         },
         onPullDownRefresh() {
             console.log('上拉刷新...');
             uni.stopPullDownRefresh();
         },
        methods: {   
            async loadData() {  
				var _this=this;
				console.log('===userInfo==='+this.hasLogin);
				console.log(this.userInfo);
				console.log(this.userInfo.token);
                if (!_this.hasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					_this.$Request.post('member_index').then(res => {
						if (res.code == 200) {
							console.log(res);
							//this.login(res.datas);
							_this.memberInfo = res.datas.member_info;
							_this.artlist =res.datas.artlist;
							_this.sysnum =res.datas.sysnum;
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:2000
							});
							uni.navigateTo({
								url:'login',
							});
						}
					});
					
				}
				// 我的收藏
				_this.$Request.post('member_favorites_store/allfavnum').then(res => {
						console.log(res);
						_this.allfavnum =res.datas.allfavnum;
						console.log(_this.allfavnum);
				});
				//我的评价
				_this.$Request.post('member_evaluate/listnum').then(res => {
						console.log(res);
						_this.pinglunnum =res.datas.allnum;
				});
            },
			toPage: function(url) {
				console.log(url)
				uni.navigateTo({
					url:url
				})	
			},
			toTab:function(url) {
				// uni.switchTab({ 
				// 	url:url
				// }); 
				uni.switchTab({ 
					url:url,
					success:(res)=> { 
						 var page = getCurrentPages().pop();
						 if (page == undefined || page == null) return;
						 page.onLoad();
					  }
				}); 
			},
			toInvitePage: function(url) {
				/* if(this.memberInfo.is_realverify!='1'){
					uni.showToast({
						icon: 'none',
						title:'请先实名验证！',
						duration:2000,
						success: function () {
							 setTimeout(function () {
								uni.navigateTo({
									url:'set/realverify',
								});
							 }, 1500);
						}
					});
				}else{ */
					uni.navigateTo({
						url:url,
					})
				//}
			}
			 
        }  
    }  
